body {
  background-color: #070e18;
}

.background {
  position: fixed;
  width: 100%;
  height: 100%;
  background-repeat: repeat;
  mix-blend-mode: overlay;
  filter: blur(5px) brightness(200%);
  z-index: -1000;
}

.header {
  opacity: 0;
  transform: translateY(25px);
  animation: fadeInMoveUp 0.5s forwards;
}

.hidden {
  opacity: 0;
}

.gallery {
  padding: 1rem;
  overflow: hidden;
}

.gallery-item {
  transition: all 0.1s ease-in-out;
}

.gallery-item:hover {
  transform: scale(1.05);
}

@keyframes fadeInMoveUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

footer {
  border-top: 1px solid #292929;
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

input {
  padding: 0.5rem;
  font-family: "Quicksand", sans-serif;
  background-color: #e9f3f7;
  border: 1px solid #070e18;
  border-radius: 5px;
}

textarea {
  padding: 0.5rem;
  font-family: "Quicksand", sans-serif;
  background-color: #e9f3f7;
  border: 1px solid #070e18;
  border-radius: 5px;
}

.form-button {
  width: 20%;
  transition: all 0.1s ease-in-out;
}

.form-button:hover {
  background-color: #60afff;
  color: #070e18;
}

.form-button:active {
  transform: scale(0.975);
}
